import {useEffect, useState} from 'react';
function Axios(){

    const [img,setimg] = useState([]);

    const axios = require('axios');

    useEffect(()=>{
        axios.get("https://dog.ceo/api/breeds/image/random")
        .then((response)=>{
            setimg(response.data.message)
        })
    },[])

    return(
        <>
        <h1>Axios</h1>
        <img src={img} alt=''/>
        </>
    )
}
export default Axios;